.bottom-menu-container
  width: 100vw
  height: 96px
  position: fixed
  bottom: 0
  left: 0
  padding: 16px
  display: flex
  align-items: stretch
  justify-content: stretch
  box-sizing: border-box

  .bottom-menu
    position: relative
    background: rgba(128, 128, 128, 0.75)
    border-radius: 8px
    width: 100%
    height: 100%
    display: flex
    flex-direction: row
    align-items: center
    justify-content: center
    gap: 9vw

    button
      background: none
      border: none
      display: flex
      flex-direction: column
      align-items: center
      justify-content: center

      img
        height: 32px

      p
        margin: 0
        font-weight: 800
        color: #E3EBC6

      .text-active
        color: white


