.app
  background-image: url(../../public/images/background.png)
  background-position: center
  display: flex
  flex-direction: column
  gap: 0
  align-items: center
  justify-content: center
  width: 100vw
  height: 100vh

.golden-strip
  background: linear-gradient(90deg, rgba(232,200,85,1) 0%, rgba(130,112,48,1) 100%)
  width: 400px
  height: 50px
  top: 32px
  right: -90px
  transform: rotate(25deg)
  position: absolute
  display: flex
  align-items: center
  justify-content: center
  mix-blend-mode: screen
  color: black
  overflow: clip

  p
    font-weight: 600
    font-size: 18px
    text-align: center
