.bottom-menu-page
  position: fixed
  bottom: 0
  left: 0
  width: 100vw
  height: 97vh
  background: rgba(64, 64, 64, 0.9)

  >*
    margin: 16px
    color: white

  .header
    background: linear-gradient(90deg, rgba(254,253,253,1) 0%, rgba(152,151,151,1) 100%)
    -webkit-background-clip: text
    -webkit-text-fill-color: transparent
    text-align: center
    line-height: 1.5
