@import "../Istok"

.Tap
  position: relative
  width: 65vw
  height: 65vw

  *
    position: absolute
    top: 0
    bottom: 0
    left: 0
    right: 0
    margin: auto
    display: block

  .image
    width: 62vw

  .image:active
    transform: scale(97%)

.app > .lvl
  text-align: center
  font-family: Istok, sans-serif
  font-size: 42px
  background: linear-gradient(0.25turn, #E86666, #E4D379)
  -webkit-background-clip: text
  -webkit-text-fill-color: transparent

.big-shadow
  width: 59vw
  height: 59vw
  border-radius: 50%