@import "../../Istok"

.multitap-button
  width: 281px
  height: 84px
  display: block
  background: linear-gradient(90deg, rgba(255,229,0,1) 0%, rgba(70,69,27,1) 100%)
  border: none
  border-radius: 20px
  margin: 0
  padding: 0
  position: relative

  &.unavailable
    filter: grayscale(80%)

  .lvl
    color: black
    font-family: 'Istok', sans-serif
    font-size: 45px
    position: absolute
    top: 12px
    left: 12px
    margin: 0

  .multitap-scale
    font-size: 12px
    font-family: 'Istok', sans-serif
    position: absolute
    top: 0
    left: 150px

  .price-container
    position: absolute
    top: 40px
    left: 140px
    background: #D9D9D9
    width: 120px
    height: 30px
    border-radius: 10px
    display: flex
    align-items: center
    justify-content: center

    .price
      font-family: 'Istok', sans-serif
      font-weight: 700
      font-size: 14px
      margin: 0
      padding: 0

